import React, { useState } from "react"
import { BrowserRouter as Router } from "react-router-dom"
import { createBrowserHistory } from "history"
import AppFrame from "./AppFrame"
import MyDrawer from "./MyDrawer"
import MyMain from "./MyMain"
import UseDataApi from './UseDataApi'


const loadLocationList=()=>{
  let locations = localStorage.getItem('locationList');
  if (locations) {
    try {
      locations = JSON.parse(locations);
    } catch (ex) {
      locations = {};
    }
  }
  if (!locations || Object.keys(locations).length === 0) {
    const key = 1609348;
    locations = {};
    locations[key] = {id: 1609348};
  }
  console.log(JSON.stringify(locations))
  return locations;
}


const AppBar=({ variant }) =>{

  const [drawer, setDrawer] = useState(false);
  const [title, setTitle] = useState("Weather");
  const [isAdd, setIsAdd]=useState(false)
  const [cityList, setCityList]=useState(loadLocationList())
  const [open, setOpen] = React.useState(false);
  const [selectedValue, setSelectedValue] = React.useState('');

  const toggleDrawer = () => {
    setDrawer(!drawer);
  };

  const onItemClick = title => () => {
    setTitle(title);
    setDrawer(variant === "temporary" ? false : drawer);
    setDrawer(!drawer);
  };

const history = createBrowserHistory();

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = (value) => {
		setOpen(false);
		setSelectedValue(value);
		let newCityList=cityList?cityList:{}
	    let key = value
        newCityList[key] = {id: value};
		saveLocationList(newCityList)
		setCityList(newCityList)
		window.location.reload(false);
	};

  const removeAllLocation=()=>{
		let newCityList=[]
		saveLocationList(newCityList);
		setCityList(newCityList)
		window.location.reload(false);
		}
	
	const  saveLocationList=(locations)=>{
	  let newList = JSON.stringify(locations);
	  localStorage.setItem('locationList', newList);
	}

	const getQuery=
		Object.keys(cityList).map((cardCity, key)=>{
					return (
				     'https://api.openweathermap.org/data/2.5/forecast?id='+cardCity+'&&APPID=a6a8579dd770e584e7e7b568d2539b4d'
				)
			})
		
  const [{ data, isLoading, isError}] = UseDataApi(getQuery)


  return (
    <div >

    <Router history={history}>
      <AppFrame 
	  		isAdd={isAdd} 
			setIsAdd={setIsAdd} 
			title={title} 
			setTitle={setTitle}
			onMenuClick={toggleDrawer} 
			cityList={cityList}
			setCityList={setCityList}
			handleClickOpen={handleClickOpen}
			handleClose={handleClose}
			saveLocationList={saveLocationList}
			removeAllLocation={removeAllLocation}
		/>

	  <MyMain
	  	cityList={cityList}
		setCityList={setCityList}
		handleClickOpen={handleClickOpen}
		handleClose={handleClose}
		saveLocationList={saveLocationList}
		open={open}
		setOpen={setOpen}
		selectedValue={selectedValue}
		setSelectedValue={setSelectedValue}
		data={data}
	  />
	  </Router>

    </div>
  );
}

export default AppBar
