import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import RemoveIcon from '@material-ui/icons/Remove';
import AccountCircle from '@material-ui/icons/AccountCircle';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RefreshIcon from '@material-ui/icons/Refresh';
import MoreIcon from '@material-ui/icons/MoreVert';
import useStyles from './useStyles';

const AppFrame=props=>{
  const classes = useStyles();
    const { isAdd, title, cityList, setCityList, onMenuClick, handleClickOpen, saveLocationList, removeAllLocation
			} = props
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const refreshPage=()=>{
    window.location.reload(false);
  }
  

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
		
  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Refresh</MenuItem>
      <MenuItem onClick={removeAllLocation}>Remove All Cities</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem  onClick={handleClickOpen}>
        <IconButton id="butAdd" aria-label="add-cities" color="inherit">
          <Badge badgeContent={Object.keys(cityList).length} color="secondary">
            <AddCircleIcon />
          </Badge>
        </IconButton>
        <p>Add City</p>
      </MenuItem>
      <MenuItem onClick={refreshPage}>
		<IconButton id="butRefresh" aria-label="refresh" color="inherit" size="medium">
            <RefreshIcon />
        </IconButton>
        <p>Refresh</p>
      </MenuItem>
	    <MenuItem onClick={removeAllLocation}>
		<IconButton id="butRemove" aria-label="remove all cities" color="inherit" size="medium">
            <RemoveIcon />
        </IconButton>
        <p>Remove All Cities</p>
      </MenuItem>
    </Menu>
  );

  return (
	<React.Fragment>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="Menu"
			size="medium"
			onClick={onMenuClick}
          >
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
			  {title}
          </Typography>
		  {isAdd&&
			  <div className={classes.search}>
				<div className={classes.searchIcon}>
				  <SearchIcon />
				</div>
				<InputBase
				  placeholder="Searchâ€¦"
				  classes={{
					root: classes.inputRoot,
					input: classes.inputInput,
				  }}
				  inputProps={{ 'aria-label': 'search' }}
				/>
			  </div>
		  }
		
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <IconButton  id="butAdd"  color="inherit" size="medium" onClick={handleClickOpen}>
              <Badge badgeContent={Object.keys(cityList).length} color="secondary">
                <AddCircleIcon />
              </Badge>
            </IconButton>
			<IconButton  id="butRefresh"  color="inherit" size="medium" onClick={refreshPage}>
                <RefreshIcon />
            </IconButton>
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
			  size="medium"
            >
              <AccountCircle />
            </IconButton>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
			  size="medium"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </React.Fragment>
  );
}

AppFrame.propTypes = {
  isAdd: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  cityList: PropTypes.object.isRequired,
  setCityList: PropTypes.func.isRequired,
  onMenuClick: PropTypes.func.isRequired,
  handleClickOpen: PropTypes.func.isRequired,
  saveLocationList: PropTypes.func.isRequired,
  removeAllLocation: PropTypes.func.isRequired,
};
export default AppFrame