import React, { Suspense, lazy } from "react"
import { Route } from "react-router-dom"
import PropTypes from 'prop-types'
import styles from "./styles"
const ShowCities = lazy(() => import('../pages/ShowCities')) 
const Home = lazy(() => import("../pages/Home"))
const Grid = lazy(() => import('../pages/Grid'))

const MyMain = 
  props => {
      const { cityList, setCityList, handleClickOpen, handleClose, saveLocationList, open, setOpen, selectedValue, setSelectedValue, data
			} = props
	
	return ( 
      <main>
	      <Suspense fallback={<div>Loading...</div>}>
			  <Route exact path="/">
				<ShowCities 
						cityList={cityList}
						setCityList={setCityList}
						handleClickOpen={handleClickOpen}
						handleClose={handleClose}
						saveLocationList={saveLocationList}
						open={open}
						selectedValue={selectedValue}
						data={data}
				/>
			  </Route>
			  <Route path="/grid"> 
				<Grid/>
			   </Route>	 
			  <Route path="/showcities"> 
				<ShowCities 
						cityList={cityList}
						setCityList={setCityList}
						handleClickOpen={handleClickOpen}
						handleClose={handleClose}
						saveLocationList={saveLocationList}
						open={open}
						selectedValue={selectedValue}
						data={data}
				/>
			   </Route>
			  <Route>
				<ShowCities 
						cityList={cityList}
						setCityList={setCityList}
						handleClickOpen={handleClickOpen}
						handleClose={handleClose}
						saveLocationList={saveLocationList}
						open={open}
						selectedValue={selectedValue}
						data={data}
				/>
			  </Route>			   
			  </Suspense>
      </main>
	  )
  }
MyMain.propTypes = {
  cityList: PropTypes.object.isRequired,
  setCityList: PropTypes.func.isRequired,
  handleClickOpen: PropTypes.func.isRequired,
  handleClose: PropTypes.func.isRequired,
  saveLocationList: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired,
  selectedValue: PropTypes.string.isRequired,
  setSelectedValue: PropTypes.func.isRequired,
  data: PropTypes.array.isRequired
}

export default MyMain
