import { useEffect, useReducer } from 'react';
import axios from 'axios';

const dataFetchReducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_INIT':
      return {
        ...state,
        isLoading: true,
        isError: false
      };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        data: action.payload,
        isError: false,
		isLoading: false,
      };
    case 'FETCH_FAILURE':
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    default:
      throw new Error();
  }
};

const UseDataApi = (initialOptions) => {
  const options = initialOptions
  const [state, dispatch] = useReducer(dataFetchReducer, {
    data: [],
	isError: false,
    isLoading: false
  });

const promiseArray = options.map(option=>axios.get(option))


  useEffect(() => {
	  let didCancel = false;
	  const fetchData = async () => {
		  dispatch({ type: 'FETCH_INIT' });
		  try {
			const result = await Promise.all(promiseArray);
				if (!didCancel) {
				dispatch({ type: 'FETCH_SUCCESS', payload: result});
				}
			  } catch (error) {
				if (!didCancel) {
				dispatch({ type: 'FETCH_FAILURE' });
			 }
		   }
		 };
    fetchData();
		return () => {
		  didCancel = true;
		};
		}, []);
	return [state];
};
			
export default UseDataApi;